@import '../core/variables.scss';



.header {
  min-height: 20vh;

  &__title {
    font-family: $fontHarry;
    font-weight: bolder;
    font-size: 4.5em;
    color: $colorGold;
    text-align: center;
    padding-top: 0.5em;
    letter-spacing: 10px;
    text-shadow: 0 1px 0 black, 0 2px 0 black, 0 3px 0 black, 0 4px 0 rgb(70, 70, 70), 0 5px 0 #5d5d5d, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15);

  }
}